import React, { PropTypes, Component } from 'react';
import { Image, Table, Message, Loader, Header, Form } from 'semantic-ui-react';
import ErrorMessage from 'spectrum-lightsuite/src/components/common/error_message';
import FormField from 'spectrum-lightsuite/src/components/common/form_field';

import { getKycField } from '../../helpers';

export default class Step7 extends Component {
  static propTypes = {
    formData: PropTypes.object.isRequired,
    formChange: PropTypes.func.isRequired,
    formSubmit: PropTypes.func.isRequired,
    kycSystem: PropTypes.object.isRequired,
    resetError: PropTypes.func.isRequired,
  }
  componentDidMount() {
    this.props.resetError();
    this.props.formChange({ otp_pin: '' });
  }
  render() {
    const { kycSystem: { kycForm, kycLabels }, formChange, formData, formSubmit } = this.props;
    return (
      <Form>
        <Header
          content="Confirm Details"
          subheader="It's time to submit your info! Please ensure it's all correct before continuing"
        />
        {kycForm.submitted ?
          <Message
            positive
            icon="checkmark"
            header="Verification Request Submitted!"
            content="Thank you. We have received your request and it will now be manually processed."
          />
          :
          <div>
            <Table definition>
              <Table.Body>
                <Table.Row>
                  <Table.Cell>First Name</Table.Cell>
                  <Table.Cell>{formData.first_name || ''}</Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>Last Name</Table.Cell>
                  <Table.Cell>{formData.last_name || ''}</Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>Gender</Table.Cell>
                  <Table.Cell>{getKycField(kycLabels, 'genders', formData.sex)}</Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>Phone Number</Table.Cell>
                  <Table.Cell>{formData.phone_number || ''}</Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>Brithday</Table.Cell>
                  <Table.Cell>{formData.birthday || ''}</Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>Nationality</Table.Cell>
                  <Table.Cell>{getKycField(kycLabels, 'countries', formData.nationality)}</Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>Address Line 1</Table.Cell>
                  <Table.Cell>{formData.address_line_one || ''}</Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>Address Line 2</Table.Cell>
                  <Table.Cell>{formData.address_line_two || ''}</Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>City</Table.Cell>
                  <Table.Cell>{formData.city || ''}</Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>State</Table.Cell>
                  <Table.Cell>{formData.state || ''}</Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>Country of Residence</Table.Cell>
                  <Table.Cell>{getKycField(kycLabels, 'countries', formData.country)}</Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>Postal Code</Table.Cell>
                  <Table.Cell>{formData.postal_code || ''}</Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>Employment Status</Table.Cell>
                  <Table.Cell>{getKycField(kycLabels, 'employment_types', formData.employment_status)}</Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>Source of Funds</Table.Cell>
                  <Table.Cell>{getKycField(kycLabels, 'source_of_fund_types', formData.source_of_funds)}</Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>Income Range</Table.Cell>
                  <Table.Cell>{getKycField(kycLabels, 'income_ranges', formData.income_range)}</Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>ID Type</Table.Cell>
                  <Table.Cell>{getKycField(kycLabels, 'identification_proof_types', formData.identification_type)}</Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>Proof of Residence Type</Table.Cell>
                  <Table.Cell>{getKycField(kycLabels, 'residence_proof_types', formData.residence_proof_type)}</Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>ID Proof</Table.Cell>
                  <Table.Cell>
                    {formData.identification_proof && <Image size="small" src={formData.identification_proof.data} alt="" />}
                  </Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>Residence Proof</Table.Cell>
                  <Table.Cell>
                    {formData.residence_proof && <Image size="small" src={formData.residence_proof.data} alt="" />}
                  </Table.Cell>
                </Table.Row>
                <Table.Row>
                  <Table.Cell>Webcam Proof</Table.Cell>
                  <Table.Cell>
                    {formData.identification_pose && <Image size="small" src={formData.identification_pose.data} alt="" />}
                  </Table.Cell>
                </Table.Row>
              </Table.Body>
            </Table>
            {kycForm.error && (
              <ErrorMessage content={kycForm.error} />
            )}
            {kycForm.submitting ?
              <Loader inline="centered" active size="huge" />
              :
              <FormField
                type="number"
                label="Enter 2FA Code"
                name="otp_pin"
                placeholder="Enter 6 digit 2FA code"
                size="huge"
                action={{
                  color: 'green',
                  icon: 'checkmark',
                  content: 'Details Confirmed & Submit',
                  onClick: formSubmit,
                }}
                {...{ formData, formChange }}
              />
            }
          </div>
        }
      </Form>
    );
  }
}



